export class WidgetLeft extends Component{
    state = {
        icons: [
            "./icons/toogle_icon.png",
            "./icons/home.png",
            "./icons/chat.png",
            "./icons/more.png"
        ],
        expend: "toogle_wrap",
        collapse:"expend"
    };

    collapse = () => {
        if (this.state.collapse === "expend"){
            this.setState({collapse:"expend show animated flipInX",expend:"toogle_wrap hide"});
        }else {
            this.setState({collapse:"expend",expend:"toogle_wrap animated zoomIn"});
        }
    };

    blank = () => {};

    render() {
        return(
            <div className="left_side_widget">
                <div className={this.state.expend}>
                    {this.state.icons.map((icon, i) => {
                        const toogleFooter = i === (this.state.icons.length - 1)? this.props.toogle:this.blank;
                        if (i === 0) {
                            return(
                                <li className="toogle_icon" key={i} onClick={this.collapse}>
                                    <img src={icon} alt="icon" />
                                </li>
                            );
                        } else if(i !== (this.state.icons.length - 1)){
                            const path = (i !== 1)?"/contact":"/homify/";
                            return (
                                <Link to={path}>
                                    <li key={i} onClick={toogleFooter}>
                                        <img src={icon} alt="icon" />
                                    </li>
                                </Link>
                            );
                        } else {
                            return (
                                <li key={i} onClick={toogleFooter} className="more">
                                    <img src={icon} alt="icon" />
                                </li>
                            );
                        }
                    })}
                </div>
                <div className={this.state.collapse} onClick={this.collapse}>
                    <img src="./icons/expend_more.png" alt="expend_more" />
                </div>
            </div>
        )
    };
}