import React, {Component} from 'react';
import Footer from "./components/Footer";
import Home from "./components/pages/Home";
import Contact from "./components/pages/Contact";
import NotFound from "./components/pages/NotFound";
import {WidgetLeft} from "./components/Widget";
import {library} from "@fortawesome/fontawesome-svg-core";
import {faCog, faBookmark,faShareSquare} from "@fortawesome/free-solid-svg-icons";
import {BrowserRouter, Route, Switch} from "react-router-dom";
import './App.css';

library.add(faCog,faBookmark,faShareSquare);

class App extends Component {
    state = {
        toogleFooterClass: ""
    };
  toogleFooter = () => {
      const state = (this.state.toogleFooterClass === "") ?"animated fadeInUp":"";
      this.setState({toogleFooterClass: state});
  };
  render() {
    return (
        <BrowserRouter>
            <React.Fragment>
                <div className="App">
                    <div className="wrap">
                        <Switch>
                            <Route path="/homify/" component={Home} exact/>
                            <Route path="/contact" component={Contact}/>
                            <Route component={NotFound} />
                        </Switch>
                        <WidgetLeft toogle={this.toogleFooter}/>
                    </div>
                    <Footer className={this.state.toogleFooterClass}/>
                </div>
            </React.Fragment>
        </BrowserRouter>
    );
  }
}

export default App;
